import {LogoTitle, InputCard, SubmitBtn, AuthContainer} from "../../components/AuthInput"
import { useNavigate  } from "react-router-dom"
import { useAuth } from "../../contexts/AuthContext"
import { useState, useEffect } from "react"
import Swal from 'sweetalert2';

const Register = () => {
  const go = useNavigate()
  const { signUp, isAuthenticated } = useAuth()
  const [ name, setName ] = useState("")
  const [ email, setEmail ] = useState("")
  const [ password, setPassword ] = useState("")
  const [ passwordCheck, setPasswordCheck ] = useState("")
  const [ isSubmitting, setIsSubmitting ] = useState(false)
  const [ showErrorMsg, setShowErrorMsg ] = useState(false)
  const [ errorMsg, setErrorMsg ] = useState("")

  async function handleSignUp() {
    setIsSubmitting(true)
    // 
    if (
      name.length === 0 ||
      password.length === 0 ||
      email.length === 0 ||
      passwordCheck.length === 0 ||
      password.length === 0
    ) {
      setShowErrorMsg(true)
      setErrorMsg('!')
      setTimeout(() => {
        setIsSubmitting(false)
      }, 1000)
      return
    }
    // 
    if(password.trim() !== passwordCheck.trim()){
      setShowErrorMsg(true)
      setErrorMsg('!')
      setTimeout(() => {
        setIsSubmitting(false)
      }, 1000)
      return
    }
    // 
    if(name.trim().length > 15){
      setShowErrorMsg(true)
      setErrorMsg('15')
      setTimeout(() => {
        setIsSubmitting(false)
      }, 1000)
      return
    }
    if(password.trim().length > 15 || passwordCheck.trim().length > 15){
      setShowErrorMsg(true)
      setErrorMsg('15')
      setTimeout(() => {
        setIsSubmitting(false)
      }, 1000)
      return
    }
    

    const { success, message} = await signUp({ name, email, password })
    if(success){
      setIsSubmitting(false)
      Swal.fire({
        position: 'top',
        title: '',
        icon: 'success',
        showConfirmButton: true,
        confirmButtonColor: "green"
      })
      go("/login")
      return
    }
    setShowErrorMsg(true)
    setErrorMsg(message.data.message)
    setIsSubmitting(false)
  }

   // 
  useEffect(() => {
    return () => {
      setErrorMsg("")
      setShowErrorMsg(false)
    }
  }, [])

  useEffect(() => {
    if (isAuthenticated) {
      go('/main')
    }
  }, [go, isAuthenticated])

  return (
    <AuthContainer>
        <LogoTitle title=""/>
        {showErrorMsg && <h4 className="text-lg text-red-500 text-center font-bold ">&#9883; {errorMsg}!</h4>}
        <div className="w-full ">
          <InputCard 
            label=" Name" 
            placeholder="Line" 
            type="text" name="name" 
            onChange={(inputValue) => setName(inputValue)} 
            disabled={isSubmitting ? true : false}/>
          <InputCard 
            label=" Email" 
            placeholder="" 
            type="email" 
            name="email" 
            onChange={(inputValue) => setEmail(inputValue)} 
            disabled={isSubmitting ? true : false}/>
          <InputCard 
            label=" Password" 
            placeholder="" 
            type="password" 
            name="password" 
            onChange={(inputValue) => setPassword(inputValue)} 
            disabled={isSubmitting ? true : false}/>
          <InputCard 
            label=" Confirm password" 
            placeholder="" 
            type="password" 
            name="passwordCheck" 
            onChange={(inputValue) => setPasswordCheck(inputValue)} 
            disabled={isSubmitting ? true : false}/>
        </div>

        <div className="w-4/5 mx-auto relative">
            <SubmitBtn submit=" Sign Up" onSubmit={handleSignUp}/>
            {isSubmitting && <svg className="absolute right-5 top-1/4 animate-spin h-5 w-5 border-slate-500 border-t-slate-200 rounded-full border-2" viewBox="0 0 24 24"></svg>}
        </div>

        <div className="w-4/5 mx-auto mt-4  ">
          <p className="text-center link" onClick={() => go("/login")}></p>
        </div>
    </AuthContainer>

  )
}

export default Register