import {LogoTitle, InputCard, SubmitBtn, AuthContainer} from "../../components/AuthInput"
import { useNavigate  } from "react-router-dom"
import { useState, useEffect } from "react"
import { useAuth } from "../../contexts/AuthContext"
import Swal from "sweetalert2"

const Login = () => {
  const go = useNavigate()
  const [ email, setEmail ] = useState("")
  const [ password, setPassword ] = useState("")
  const { login, isAuthenticated } = useAuth()
  const [ isSubmitting, setIsSubmitting ] = useState(false)

  async function handleLogin(){
    setIsSubmitting(true)
    if(email.length === 0 || password.length === 0){
      Swal.fire({
        position: 'top',
        title: "",
        icon: 'error',
        showConfirmButton: true,
        confirmButtonColor: "gray"
      })
      setTimeout(() => {
        setIsSubmitting(false)
      }, 1000)
      return
    }
    
    const { success, message } = await login({email, password})
  
    if(success){
      setIsSubmitting(false)
      Swal.fire({
        position: 'top',
        title: '',
        timer: 1000,
        icon: 'success',
        showConfirmButton: false,
      })
      return
    }else{
      setIsSubmitting(false)
      const data = message?.status === 401 ? "" : message?.data.message  
      Swal.fire({
        position: 'top',
        title: data || "",
        icon: 'error',
        showConfirmButton: true,
        confirmButtonColor: "gray"
      })
    }
  }

  // 
  useEffect(() => {
    if(isAuthenticated){
      go("/main")
    }
  }, [go, isAuthenticated])

  return (
    <AuthContainer>
        <LogoTitle title=" Stock Report"/>
        
        <div className="w-full ">
          <InputCard 
            label=" Account" 
            placeholder="" 
            type="text" 
            name="email" 
            onChange={(inputValue) => setEmail(inputValue)} 
            disabled={isSubmitting ? true : false}/>
          <InputCard 
            label=" Password" 
            placeholder="" 
            type="password" 
            name="password" 
            onChange={(inputValue) => setPassword(inputValue)} 
            disabled={isSubmitting ? true : false}/>
        </div>

        <div className="w-4/5 mx-auto relative">
          <SubmitBtn submit=" Login" onSubmit={handleLogin}/>
          {isSubmitting && <svg className="absolute right-5 top-1/4 animate-spin h-5 w-5 border-slate-500 border-t-slate-200 rounded-full border-2" viewBox="0 0 24 24"></svg>}
        </div>

        <div className="w-4/5 mx-auto mt-4 flex ">
          <p className="flex-1 text-end link" onClick={() => go("/register")}></p>
          <span className="block px-[20px]">&#8729;</span>
          <p className="link" onClick={() => go("/admin/login")}></p>
        </div>
    </AuthContainer>
  )
}

export default Login