// 
export function convertTimeFromMs(time){
    //
    let milliseconds = Date.parse(time) - Date.now()
    //
    const NegativeDays = Math.trunc(milliseconds / 86400000)
    const days = NegativeDays * -1
    milliseconds = NegativeDays * 86400000 - milliseconds
    //
    const hours = Math.trunc(milliseconds / 3600000)
    milliseconds = hours * 3600000 - milliseconds
    return {
        days,
        hours,
    }
  }

//  => 
// 
export function getTimeDiffTransForm(targetTime){
  let displayTime
  if(targetTime){
    const {hours, days} = convertTimeFromMs(targetTime)
    if(hours !== 0){
      displayTime = days === 0 ?  (hours + "") : days + "" + hours + ""
    }else if(hours === 0 && days === 0){
      displayTime = ""
    }else if(hours === 0){
      displayTime = days + ""
    }
  }
  return displayTime
}

//  => 
//  10:0520211110
export function getDateTransform(date){
  let hour;
  let result;
  const  newDate = new Date(date)
  if(newDate.getHours() - 12 === 0){
    hour = " 12"
  }else if(newDate.getHours() -12 > 0 && newDate.getHours() - 12 < 12){
    hour = " " + (newDate.getHours() -12)
  }else if(newDate.getHours() - 12 < 0){
    hour = " "  + newDate.getHours()
  }else if(newDate.getHours() - 12 === 12){
    hour = " 12"
  }
  if(newDate?.getMonth()){
    result = 
      newDate?.getFullYear() + "" 
      + (newDate?.getMonth()+1)+""
      + newDate?.getDate() + "  "
      + hour + ":" + newDate?.getMinutes()
  }
  return result
}